<h1 id="分账" tabindex="-1"><a class="header-anchor" href="#分账" aria-hidden="true">#</a> 分账</h1>
<blockquote>
<p>官方文档 https://pay.weixin.qq.com/wiki/doc/api/allocation.php?chapter=27_1&amp;index=1</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Factory</span><span class="token punctuation">;</span>
<span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
	<span class="token string single-quoted-string">'app_id'</span>     <span class="token operator">=></span> <span class="token string single-quoted-string">'***'</span><span class="token punctuation">,</span>
	<span class="token string double-quoted-string">"secret"</span>     <span class="token operator">=></span> <span class="token string double-quoted-string">"***"</span><span class="token punctuation">,</span>
	<span class="token string single-quoted-string">'mch_id'</span>     <span class="token operator">=></span> <span class="token string single-quoted-string">'***'</span><span class="token punctuation">,</span>
	<span class="token string single-quoted-string">'key'</span>        <span class="token operator">=></span> <span class="token string single-quoted-string">'***'</span><span class="token punctuation">,</span>
	<span class="token string single-quoted-string">'cert_path'</span>  <span class="token operator">=></span> <span class="token string single-quoted-string">'cert.pem'</span><span class="token punctuation">,</span>
	<span class="token string single-quoted-string">'key_path'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'key.pem'</span><span class="token punctuation">,</span>
	<span class="token string single-quoted-string">'notify_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://***.com/notify.php'</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$payment</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">payment</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><h3 id="添加接收方" tabindex="-1"><a class="header-anchor" href="#添加接收方" aria-hidden="true">#</a> 添加接收方</h3>
<blockquote>
<p>商户发起添加分账接收方请求，后续可通过发起分账请求将结算后的钱分到该分账接收方。</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$receiver</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
	<span class="token string double-quoted-string">"type"</span>          <span class="token operator">=></span> <span class="token string double-quoted-string">"PERSONAL_OPENID"</span><span class="token punctuation">,</span>
	<span class="token string double-quoted-string">"account"</span>       <span class="token operator">=></span> <span class="token string double-quoted-string">"…………"</span><span class="token punctuation">,</span><span class="token comment">//PERSONAL_OPENID：个人openid</span>
	<span class="token string double-quoted-string">"name"</span>          <span class="token operator">=></span> <span class="token string double-quoted-string">"张三"</span><span class="token punctuation">,</span><span class="token comment">//接收方真实姓名</span>
	<span class="token string double-quoted-string">"relation_type"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"PARTNER"</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">addReceiver</span><span class="token punctuation">(</span><span class="token variable">$receiver</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$receiver</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
	<span class="token string double-quoted-string">"type"</span>          <span class="token operator">=></span> <span class="token string double-quoted-string">"MERCHANT_ID"</span><span class="token punctuation">,</span>
	<span class="token string double-quoted-string">"account"</span>       <span class="token operator">=></span> <span class="token string double-quoted-string">"132456798"</span><span class="token punctuation">,</span><span class="token comment">//MERCHANT_ID：商户ID</span>
	<span class="token string double-quoted-string">"name"</span>          <span class="token operator">=></span> <span class="token string double-quoted-string">"商户全称"</span><span class="token punctuation">,</span><span class="token comment">//商户全称</span>
	<span class="token string double-quoted-string">"relation_type"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"PARTNER"</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">addReceiver</span><span class="token punctuation">(</span><span class="token variable">$receiver</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><h3 id="删除接收方" tabindex="-1"><a class="header-anchor" href="#删除接收方" aria-hidden="true">#</a> 删除接收方</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">deleteReceiver</span><span class="token punctuation">(</span><span class="token variable">$receiver</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="单次分账" tabindex="-1"><a class="header-anchor" href="#单次分账" aria-hidden="true">#</a> 单次分账</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$transaction_id</span> <span class="token operator">=</span> <span class="token string double-quoted-string">"***"</span><span class="token punctuation">;</span>
<span class="token variable">$out_trade_no</span> <span class="token operator">=</span> <span class="token string double-quoted-string">"***"</span><span class="token punctuation">;</span>
<span class="token variable">$receivers</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
	<span class="token punctuation">[</span>
		<span class="token string double-quoted-string">"type"</span>        <span class="token operator">=></span> <span class="token string double-quoted-string">"PERSONAL_OPENID"</span><span class="token punctuation">,</span>
		<span class="token string double-quoted-string">"account"</span>     <span class="token operator">=></span> <span class="token string double-quoted-string">"***"</span><span class="token punctuation">,</span>
		<span class="token string double-quoted-string">"amount"</span>      <span class="token operator">=></span> <span class="token number">2</span><span class="token punctuation">,</span>
		<span class="token string double-quoted-string">"description"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"分到个人"</span>
	<span class="token punctuation">]</span><span class="token punctuation">,</span>
	<span class="token punctuation">[</span>
		<span class="token string double-quoted-string">"type"</span>        <span class="token operator">=></span> <span class="token string double-quoted-string">"MERCHANT_ID"</span><span class="token punctuation">,</span>
		<span class="token string double-quoted-string">"account"</span>     <span class="token operator">=></span> <span class="token string double-quoted-string">"***"</span><span class="token punctuation">,</span>
		<span class="token string double-quoted-string">"amount"</span>      <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
		<span class="token string double-quoted-string">"description"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"分到商户"</span>
	<span class="token punctuation">]</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$sharing</span> <span class="token operator">=</span> <span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">share</span><span class="token punctuation">(</span><span class="token variable">$transaction_id</span><span class="token punctuation">,</span><span class="token variable">$out_trade_no</span><span class="token punctuation">,</span><span class="token variable">$receivers</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h3 id="多次分账" tabindex="-1"><a class="header-anchor" href="#多次分账" aria-hidden="true">#</a> 多次分账</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">multiShare</span><span class="token punctuation">(</span><span class="token variable">$transaction_id</span><span class="token punctuation">,</span><span class="token variable">$out_trade_no</span><span class="token punctuation">,</span><span class="token variable">$receivers</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="多次分账完结" tabindex="-1"><a class="header-anchor" href="#多次分账完结" aria-hidden="true">#</a> 多次分账完结</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$params</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
	<span class="token string double-quoted-string">"transaction_id"</span> <span class="token operator">=></span> <span class="token string double-quoted-string">""</span><span class="token punctuation">,</span>
	<span class="token string double-quoted-string">"out_order_no"</span>   <span class="token operator">=></span> <span class="token string double-quoted-string">""</span><span class="token punctuation">,</span>
	<span class="token string double-quoted-string">"description"</span>    <span class="token operator">=></span> <span class="token string double-quoted-string">""</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">markOrderAsFinished</span><span class="token punctuation">(</span><span class="token variable">$params</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="分账查询" tabindex="-1"><a class="header-anchor" href="#分账查询" aria-hidden="true">#</a> 分账查询</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$res</span> <span class="token operator">=</span> <span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">query</span><span class="token punctuation">(</span><span class="token variable">$transaction_id</span><span class="token punctuation">,</span><span class="token variable">$out_trade_no</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><blockquote>
<p>查询结果</p>
</blockquote>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>Array
(
    [return_code] => SUCCESS
    [result_code] => SUCCESS
    [mch_id] => ***
    [nonce_str] => 38e92cbe2790642f
    [sign] => 8904B6440C58785540950F2911500F55C9A94CAC75790B0721B9AA470E6BF9A8
