</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h3 id="分账退回" tabindex="-1"><a class="header-anchor" href="#分账退回" aria-hidden="true">#</a> 分账退回</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$out_trade_no</span> <span class="token operator">=</span> <span class="token string double-quoted-string">"***"</span><span class="token punctuation">;</span><span class="token comment">//退款订单号</span>
<span class="token variable">$out_return_no</span> <span class="token operator">=</span> <span class="token string double-quoted-string">"***"</span><span class="token punctuation">;</span><span class="token comment">//系统内部退款单号</span>
<span class="token variable">$return_amount</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">;</span>
<span class="token variable">$return_account</span> <span class="token operator">=</span> <span class="token string double-quoted-string">"***-g4"</span><span class="token punctuation">;</span>
<span class="token variable">$description</span> <span class="token operator">=</span> <span class="token string double-quoted-string">"订单取消"</span><span class="token punctuation">;</span>
<span class="token variable">$payment</span><span class="token operator">-></span><span class="token property">profit_sharing</span><span class="token operator">-></span><span class="token function">returnShare</span><span class="token punctuation">(</span><span class="token variable">$out_trade_no</span><span class="token punctuation">,</span><span class="token variable">$out_return_no</span><span class="token punctuation">,</span><span class="token variable">$return_amount</span><span class="token punctuation">,</span><span class="token variable">$return_account</span><span class="token punctuation">,</span><span class="token variable">$description</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div>